import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Producto } from '../../models/producto'

@IonicPage()
@Component({
	selector: 'page-productos',
	templateUrl: 'productos.html'
})
export class ProductosPage {

	private productos:Producto[] = new Array();

	constructor(public navCtrl: NavController, public navParams: NavParams) {

		this.productos.push(new Producto("Producto 1", new Date()));
		this.productos.push(new Producto("Producto 2", new Date()));
		this.productos.push(new Producto("Producto 3", new Date()));
		this.productos.push(new Producto("Producto 4", new Date()));
		this.productos.push(new Producto("Producto 5", new Date()));

		//console.log(this);

	}

}



